/* Created with reference to Jatin Sharma's code
    https://codepen.io/j471n/pen/vYJaLvm */
/* TODO: credit that properly on web page */

.timeline {
	display: flex;
	flex-direction: column;
	position: relative;
}

/* Line in the middle */
.timeline::after {
	background-color: var(--timelineColor);
	content: '';
	position: absolute;
	left: calc(50% - 2px);
	width: 4px;
	height: calc(100% - 125px);
	margin-top: 70px;
}

.timeline-item {
	display: flex;
	justify-content: flex-end;
	margin-right: 30px;
	position: relative;
	margin: 10px 0;
	width: calc(50% - 27px);
}

.timeline-item:nth-child(odd) {
	align-self: flex-end;
	justify-content: flex-start;
	margin-left: 30px;
	margin-right: 0;
}

.timeline-item-content {
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
	border-radius: 5px;
	background-color: #fff;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	padding: 15px;
	position: relative;
	width: 400px;
	max-width: 70%;
	text-align: right;
}

/* Arrows pointing to timeline */
.timeline-item-content::after {
	content: ' ';
	background-color: #fff;
	box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2);
	position: absolute;
	right: -7.5px;
	top: calc(50% - 7.5px);
	transform: rotate(45deg);
	width: 15px;
	height: 15px;
}

.timeline-item:nth-child(odd) .timeline-item-content {
	text-align: left;
	align-items: flex-start;
}

.timeline-item:nth-child(odd) .timeline-item-content::after {
	right: auto;
	left: -7.5px;
	box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);
}

.timeline-item-content .tag {
	color: #fff;
	font-size: 12px;
	font-weight: bold;
	top: 5px;
	left: 5px;
	letter-spacing: 1px;
	padding: 5px;
	position: absolute;
	text-transform: uppercase;
}

.timeline-item:nth-child(odd) .timeline-item-content .tag {
	left: auto;
	right: 5px;
}

/* Style common bits of the item contents */
.timeline-title {
	font-weight: bold;
}

.timeline-item-content time {
    margin: 0.5em 0;

	color: #777;
	font-size: 12px;
	font-weight: bold;
}

.timeline-item-content p {
	font-size: 16px;
	line-height: 1.4em;
	margin: 1em 0 0 0;
}

.timeline-item-content a {
	font-size: 14px;
	font-weight: bold;

    margin-top: 0.8em;
}

.timeline-item .techStack img {
    margin: 0 0 0 7px;
}

.timeline-item:nth-child(odd) .techStack img {
    margin: 0 7px 0 0;
}

.timeline-item-content a::after {
	content: ' â–º';
	font-size: 12px;
}

.circle {
	background-color: #fff;
	border: 3px solid var(--timelineColor);
	border-radius: 50%;
	position: absolute;
	top: calc(50% - 13px);
	right: -40px;
	width: 20px;
	height: 20px;
	z-index: 100;
}

.timeline-item:nth-child(odd) .circle {
	right: auto;
	left: -40px;
}

@media only screen and (max-width: 1023px) {
	.timeline-item-content {
		max-width: 100%;
	}
}

/* Alternate timeline layout for smaller screens */
@media only screen and (max-width: 767px) {
    .timeline-item-content {
        width: 100%;
    }

    .circle {
        right: -50px;
    }

    .timeline-item:nth-child(odd) .circle {
        left: -50px;
    }

    /* Curvy borders */
    .timeline-item {    /* All left elements */
        margin: -5px 30px 0 30px;
        padding: 35px;
        width: calc(100% - 200px);

        top: 0;

        border-style: solid;
        border-color: var(--timelineColor);
        border-width: 5px 5px 5px 0;
        border-radius: 0 50px 50px 0;
    }

    .timeline-item:nth-child(odd) { /* All right elements */
        border-width: 5px 0 5px 5px;
        border-radius: 50px 0 0 50px;
    }

    .timeline-item:first-child { /* First element */
        border-style: solid dotted;
        border-top: 0;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }

    .timeline-item:last-child { /* Last element */
        border-style: solid dotted;
        border-bottom: 0;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    .timeline::before { /* Trailing off dotted line */
        content: '';
        border-style: none none none dotted;
        border-color: var(--timelineColor);
        border-width: 5px;
        width: calc(100% - 65px);
        height: 80px;

        align-self: center;
    }

    /* Align all card contents in alternating style */
    .timeline-item-content {
        text-align: right;
    }

	.timeline-item:nth-child(odd) .timeline-item-content {
		text-align: left;
	}

    .timeline-item {
        align-self: flex-end;
    }

    .timeline-item:nth-child(odd) {
        align-self: flex-start;
    }

    /* Delete center line */
    .timeline::after {
        display: none;
    }
}
